.user-modal {
  visibility: hidden;
  z-index: 50; }

.left-menu_header {
  width: 100%;
  display: flex;
  justify-content: start;
  align-items: center;
  align-self: center;
  margin-bottom: 0 !important;
  font-size: 16px;
  margin-left: 10px; }

.left-menu {
  outline: none !important;
  border: none;
  background-color: transparent;
  width: 100%; }

.left-menu,
.left-menu_item {
  position: relative;
  height: 45px; }

.left-menu::after {
  content: '';
  border: 0.5px solid grey;
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0; }

.left-menu_btn {
  border: none;
  background-color: transparent;
  width: 100%;
  position: relative;
  justify-content: start;
  display: flex;
  letter-spacing: 0.9px;
  font-size: 14px;
  padding-left: 20px;
  outline: none !important; }

.left-menu_toggle-icon {
  position: absolute;
  right: 10px;
  width: 15px;
  height: auto;
  display: flex;
  align-self: center;
  transform: rotate(180deg); }

.left-menu_item {
  color: black !important;
  padding: 0 0 0 20px !important;
  display: flex !important;
  align-items: center;
  outline: none;
  font-size: 13px !important;
  letter-spacing: 0.9px !important;
  border-top: none;
  border-bottom: none; }

.flex-column {
  visibility: visible; }

.client-icon {
  width: 45px;
  height: 33px;
  border-radius: 100%;
  border: transparent;
  margin-left: 10px; }

.order-left {
  padding-right: 0 !important; }

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  color: #fff;
  background-color: rgba(229, 226, 226, 0.394) !important;
  color: black !important;
  border-left: 2px solid grey;
  border-radius: 0 !important; }
