.modal-orient {
  display: flex;
  justify-content: center;
  width: 100%; }

.modal-view-bkg {
  width: 100vw;
  height: 190vh;
  background-color: black;
  opacity: 0.5;
  z-index: 20;
  visibility: hidden;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 50; }

.order-item-img {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.modal-item_order-close {
  border: none !important;
  outline: none !important; }
  .modal-item_order-close-img {
    width: 40px;
    position: absolute;
    top: -22px;
    right: -10px; }

.center-table_top {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 30%;
  color: black;
  align-items: center;
  border-bottom: 0.5px solid rgba(128, 128, 128, 0.945); }

.center-table_top_main-part {
  width: 30%; }

.center-table_top_header {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 5px; }
  .center-table_top_header-text {
    font-size: 14px;
    letter-spacing: 0.5px; }

.center-table_bottom {
  width: 100%;
  margin-bottom: 10px;
  border: none;
  margin-top: 10px; }
  .center-table_bottom .tab-content {
    padding-top: -20px !important; }

.order_cart {
  padding-left: 0 !important; }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6); }
  .modal .modal-main {
    position: fixed;
    background: white;
    width: 80%;
    height: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

.center-table {
  border-left: 0.5px solid grey;
  border-right: 0.5px solid grey;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 0 50px !important; }

.center-table-card {
  height: 385px;
  padding-right: 5px !important;
  padding-left: 5px !important;
  margin-bottom: 20px; }

.center-table-card_item {
  z-index: 100;
  height: 100%;
  border: 0.9px solid rgba(179, 179, 179, 0.735) !important;
  border-radius: 0 !important;
  box-shadow: 0.2px 1px 5px rgba(186, 186, 186, 0.633); }

.center-table-card_text {
  margin: 10px 0 0 20px !important;
  font-size: 16px !important; }

.center-table-card_text-bottom {
  margin: 10px 0 10px 20px !important;
  font-size: 16px !important; }

.center-table-card_item {
  z-index: 5;
  cursor: pointer; }

.center-table-card_item:hover img {
  height: 35%;
  z-index: 0; }

.center-table-card_item:hover .card-title {
  text-overflow: unset;
  display: block;
  white-space: normal;
  overflow: visible; }

.item-img {
  height: 75%;
  object-fit: cover;
  object-position: center;
  z-index: 2; }

.item-img-background {
  width: 100%;
  height: 35%;
  background-color: black;
  z-index: 1;
  opacity: 0.3;
  position: absolute; }

.card-title {
  z-index: 4;
  background-color: white;
  text-overflow: ellipsis;
  display: block;
  white-space: nowrap;
  overflow: hidden; }

.center-table-card_icon {
  font-size: 30px;
  font-weight: 400;
  position: absolute;
  bottom: 10px;
  right: 20px; }

.order-modal_header-text {
  position: absolute;
  padding-left: 20px;
  font-size: 29px;
  font-weight: 700;
  margin-top: 20px;
  margin-bottom: 0; }
