.order-modal {
  position: absolute;
  width: 500px;
  height: 800px;
  top: -150px;
  outline: transparent;
  z-index: 90;
  display: flex;
  justify-content: center;
  background-color: white !important; }
  .order-modal-main {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-self: center;
    align-self: center;
    align-content: center;
    text-align: center;
    height: 100%;
    position: absolute;
    top: 0;
    width: 100%; }
  .order-modal-item {
    top: 0;
    position: absolute;
    width: 100%;
    height: 50%; }
    .order-modal-item_btn {
      outline: none !important;
      border: none;
      border-radius: 0;
      position: absolute;
      width: 100%;
      height: 45px;
      background-color: white !important;
      margin-top: 188px;
      font-size: 23px;
      color: grey !important;
      left: 0;
      position: absolute;
      top: 0;
      font-size: 17px;
      padding-right: 20px;
      display: flex;
      justify-content: center;
      align-items: center; }
  .order-modal-header {
    font-size: 22px;
    font-weight: 700;
    margin-top: 100px;
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: self-start;
    align-self: start;
    text-align: start;
    padding: 50px 40px 0 20px; }
    .order-modal-header_price {
      margin-top: 15px;
      font-weight: 300; }
    .order-modal-header_text {
      font-weight: 400;
      font-size: 20px;
      margin-top: 30px; }

.order-modal-header::after {
  content: '';
  border: 0.3px solid rgba(181, 180, 180, 0.612);
  width: 100%; }

.plus-icon {
  font-weight: 500;
  color: black;
  font-size: 28px; }

.order-modal-item_calculate {
  display: flex;
  flex-direction: row;
  position: absolute;
  bottom: 82px;
  width: 100%;
  justify-content: space-between;
  padding: 0 20px; }
  .order-modal-item_calculate-left {
    font-size: 18px;
    margin-bottom: 0 !important;
    display: flex;
    align-items: center;
    padding: 0 5px; }
  .order-modal-item_calculate-right {
    display: flex;
    flex-direction: row;
    width: 30%;
    justify-content: space-between;
    align-items: center;
    font-size: 25px;
    padding-right: 15px; }

.minus_btn,
.plus_btn {
  outline: none !important;
  border: 0px solid grey;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: white;
  cursor: not-allowed !important; }

.btn_count-icon {
  font-weight: 700; }

.item_btn {
  width: 90%;
  height: 42px;
  position: absolute;
  bottom: 30px;
  color: white !important;
  font-size: 21px !important;
  font-weight: 800 !important;
  margin-left: 20px;
  display: flex !important;
  justify-content: center;
  cursor: not-allowed !important;
  align-items: center;
  padding: 0 !important; }

.plus-icon_btn {
  color: white;
  font-size: 20px;
  margin-right: 10px; }

.message {
  font-size: 12px;
  position: absolute;
  bottom: 7px;
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 0 !important; }

.order-modal-item_btn-text {
  visibility: visible; }

.modal_text_block {
  visibility: hidden; }
  .modal_text_block-top {
    outline: none !important;
    border: 0.5px solid rgba(186, 185, 185, 0.585);
    width: 88%;
    height: 40px;
    padding: 5px 0 0 10px;
    position: absolute;
    left: 20px;
    top: 10px; }
  .modal_text_block-bottom {
    margin-top: 27px;
    position: absolute;
    left: 30px;
    font-size: 10px; }
