* {
  box-sizing: border-box; }

html {
  color: #111111;
  font-size: 14px; }

.page {
  font-family: 'Raleway', sans-serif;
  margin: 70px 70px 0 70px; }

.content {
  width: 100%; }

.container {
  max-width: 1600px !important; }

.card {
  border: none; }

body,
ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure {
  margin: 0;
  padding: 0; }

ul,
ol {
  list-style: none; }

a {
  text-decoration: none;
  color: black; }

input,
button {
  font-size: 1rem;
  color: black !important;
  font-family: 'Raleway', sans-serif; }

.text-bold {
  font-weight: 800; }

.text-regular {
  font-weight: 500; }

.page-main {
  display: flex;
  justify-content: center; }

.no-padding {
  padding: 0 !important; }

.page-footer {
  bottom: 40px;
  width: 100%;
  height: auto;
  outline: none;
  margin-top: 75px;
  z-index: 1;
  font-family: 'Raleway', sans-serif; }
  .page-footer_row {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
  .page-footer_row-text {
    margin-bottom: 40px;
    margin-top: 55px;
    font-weight: 700;
    font-size: 16px; }
  .page-footer_row-input {
    width: 338px;
    height: 33px;
    margin-bottom: 5px;
    border: 1px solid black;
    padding-left: 25px;
    outline: none; }
  .page-footer_row-input:hover {
    border: 2px solid black; }
  .page-footer_row-btn {
    width: 338px;
    height: 33px;
    color: white !important;
    letter-spacing: 1px;
    font-weight: 600;
    font-size: 16px;
    border: none;
    outline: none;
    cursor: pointer;
    font-family: 'Avenir', sans-serif; }
  .page-footer_row-btn:hover {
    background-color: #4f4d4e; }

::placeholder {
  color: black;
  font-weight: 300;
  font-size: 14px;
  letter-spacing: 0.7px;
  font-family: 'Raleway', sans-serif; }

.main-page {
  height: auto; }

.page {
  margin: 0 100px;
  padding: 0;
  position: relative; }

.content {
  position: relative;
  height: auto; }
