.about-container {
  padding: 0 !important; }

.about-page {
  padding-top: 70px;
  background-color: #f7e9e4; }
  .about-page_content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
    position: relative; }
  .about-page_content-header {
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 30px;
    position: relative;
    padding-bottom: 40px;
    letter-spacing: 1px;
    margin-top: 130px; }
  .about-page_content-header::after {
    content: '';
    width: 20%;
    border: 0.5px solid black;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translate(210%, 0); }
  .about-page_content-discription {
    margin-bottom: 80px;
    font-size: 16px;
    letter-spacing: 1.1px;
    font-weight: 700; }
  .about-page_content-img {
    width: 300px;
    height: 300px; }
  .about-page_content-img_center {
    border-radius: 550px;
    width: 100%;
    height: 100%; }
  .about-page_content-text {
    width: 37.5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
    position: relative;
    font-size: 15px;
    line-height: 22px; }
  .about-page_content-text_top {
    margin: 70px 0 30px; }
  .about-page_content-text_bottom {
    margin-bottom: 130px; }
