.bottom-img_block {
  object-position: left;
  overflow: hidden; }

.bottom-img {
  position: relative;
  object-fit: cover; }

.meet-btn {
  width: 160px;
  height: 48px;
  border: none !important;
  background-color: white !important;
  font-weight: 700 !important;
  outline: none !important;
  cursor: pointer;
  letter-spacing: 0.4px;
  font-size: 16px !important;
  padding: 0 !important;
  color: black !important;
  font-family: 'Raleway', sans-serif; }

.meet-btn:hover {
  border: 1.5px solid black !important; }

.meet-btn_link {
  position: absolute;
  text-decoration: none;
  padding: 0;
  margin: 0;
  width: 160px;
  height: 48px;
  right: 60px;
  bottom: 50px; }

.social-header {
  width: 100%;
  margin-top: 40px;
  font-size: 30px; }

.text-center {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  letter-spacing: -1.5px; }

.navigate-linkOrder {
  width: 100%;
  height: 100%;
  color: black !important;
  font-size: 18px; }

.navigate-linkOrder:hover {
  text-decoration: none; }

.col-6 {
  padding: 0 0 0 0; }
