.contact-container_top {
  width: 100%;
  height: 200px;
  background-color: #f7e9e4 !important;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 !important; }
  .contact-container_top-text {
    margin-left: 30px;
    color: black;
    width: 100%;
    margin-bottom: 0 !important;
    font-size: 35px;
    letter-spacing: 0.5px; }

.contact-container_center {
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: 16px;
  letter-spacing: .7px;
  font-weight: 700;
  padding-left: 50px;
  margin-top: 25px; }
  .contact-container_center_text_left {
    margin-right: 15px; }
  .contact-container_center_text_left::after {
    content: '';
    margin-left: 15px;
    width: 20%;
    border: 0.8px solid black;
    bottom: 0;
    left: 0; }

.contact-container_line {
  margin-top: 70px;
  width: 2%;
  border-top: 1px solid black; }

.contact-container_description {
  margin-top: 50px;
  display: flex;
  justify-content: center; }
  .contact-container_description-text {
    width: 750px;
    align-items: center;
    text-align: center;
    letter-spacing: 0.7px;
    line-height: 25px;
    font-size: 15px; }

.contact-container {
  padding: 0 !important; }
